.app-wrapper {
    display: grid;
    width: 70%;
    margin: 0 auto;
    grid-template-areas:
            "header header"
            "nav content";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;

}

.app-wrapper-content {
    grid-area: content;
    background-color: #badbff;
}


